import {uniIcons} from '@dcloudio/uni-ui'
export default {
	components: {uniIcons},
	data() {
		return {
			form: {
				account: '',
				password: ''
			},
			
		};
	},
	methods: {
		submit() {
			let {account, password} = this.form
			
			if ([account, password].includes('')) {
				this.showTip()
			} else {
				wx.cloud.callFunction({
				  name: 'login',
				  data: this.form
				}).then(res => {
					this.$store.commit('setUser', res.result.res.data[0])
					if (res.result.code === 0) {
						uni.navigateTo({
							url: '../index/index'
						})
					} else {
						this.showTip()
					}
				})
				
			}
		},
		showTip() {
			uni.showToast({
				icon: 'none',
				title: '工号或密码不正确',
				duration: 1000
			})
		}
	},
	mounted() {
		
	}
}
